import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { MyApp } from './app.component';

import {
  PrincipalPage,
  AjustesPage,
  Ajustes2Page,
  ModalPage,
  Pagina2Page,
  Pagina3Page,
  TabsPage
} from '../pages/index.paginas';

@NgModule({
  declarations: [
    MyApp,
    PrincipalPage,
    AjustesPage,
    Ajustes2Page,
    ModalPage,
    Pagina2Page,
    Pagina3Page,
    TabsPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp,{
      backButtonText: 'Atras'
    }, 
{
  links: [
    { loadChildren: '../pages/ajustes2/ajustes2.module#Ajustes2PageModule', name: 'Ajustes2Page', segment: 'ajustes2', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/pagina2/pagina2.module#Pagina2PageModule', name: 'Pagina2Page', segment: 'pagina2', priority: 'low', defaultHistory: [] }
  ]
})
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    PrincipalPage,
    AjustesPage,
    Ajustes2Page,
    ModalPage,
    Pagina2Page,
    Pagina3Page,
    TabsPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



